.container {
  height: 100%;
  justify-content: center;
  align-items: center;
  gap: 30px;
  margin: auto 0;
  display: flex;
}

p {
  padding: 15px;
}

button {
  cursor: default;
  width: 150px;
  height: 50px;
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #0ff;
  border-radius: 25px;
  outline: none;
  justify-content: center;
  align-items: center;
  transition: all .3s ease-in-out;
  display: flex;
}

button:hover {
  color: #fff;
  background-color: rgba(14, 12, 12, .808);
}

button:disabled, button[disabled] {
  cursor: not-allowed;
  color: #666;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #999;
}

